
img.meta-trademark {  
  width: 20%;  
}  
  
.sortby {  
  font-weight: 300;  
  cursor: pointer;  
}  
  
.token-icon {  
  width: 28px;  
  height: 28px;  
}  
  
.download-metamask {  
  height: 30%;  
  cursor: pointer;  
  width: 70%;  
  margin: auto;  
}  
  
.is-ellipsis {  
  overflow: hidden;  
  text-overflow: ellipsis;  
}

.table th,
.table td {
    border: 1px solid #000;
    padding: 4px;
}

.table {
    width: 100%;
    border-collapse: collapse;
}

#container {
    display: flex;
    flex-direction: column;
    padding: 40px;
}

#stake-form label {
    display: block;
    margin: 10px 0 15px;
}

#staking {
    display: flex;
    flex-direction: column;
}

#stake-form {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 50px;
}

#stake-form label span {
    display: block;
    font-weight: 700;
}

#footer {
    margin-top: auto;
    background: #e7e7e7;
    padding: 30px;
    text-align: center;
}

.tabs {
    display: flex;
    align-items: stretch;
    justify-content: center;
    list-style: none;
    padding: 0;
    border-bottom: 1px solid #000;
    margin-bottom: 50px;
}

.tab {
    margin: 0 10px;
}

.tab > a {
    display: block;
    margin-bottom: -1px;
    padding: 20px 15px;
    border: 1px solid #000;
    background: #eee;
    color: #666;
    font-size: 14px;
    font-weight: 700;
    text-transform: uppercase;
    letter-spacing: 1px;
    cursor: pointer;
    min-width: 170px;
    text-align: center;
    text-decoration: none;
}

h1 a {
    text-decoration: none;
    color: #000;
}

.tab:hover a {
    color: #333;
}

.tab-content {
    display: none;
    color: #777;
}

.tab a.active {
    border-bottom-color: #fff;
    background: #fff;
    color: #222;
}

